@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300&family=Roboto:wght@100;300&display=swap);
.main-wrapper{position:absolute;top:0px;right:0px;bottom:0px;left:0px;overflow-x:hidden}.title{font-family:'Lato';font-weight:300;margin-left:50px;margin-right:50px}.title-normal{font-style:normal}.title-italic{font-style:italic}.title-l{font-size:60px}.title-m{font-size:40px}.title-s{font-size:25px}.title-link{color:white;text-decoration:none}.title-link:hover{color:grey}.paragraph{font-family:'Roboto';font-weight:300;font-size:18px;margin-left:50px;margin-right:50px;margin-top:0px;margin-bottom:0px}.spacer{margin-left:40px;margin-right:40px}.empty{margin-top:50px}.white-text{color:white}.black-text{color:black}.purple-text{color:#8B8599}.embed-container{--video--width: 1296;--video--height: 540;position:relative;padding-bottom:calc(var(--video--height) / var(--video--width) * 100%);overflow:hidden;max-width:100%}.embed-container iframe,.embed-container object,.embed-container embed{position:absolute;top:0;left:0;width:100%;height:100%}.navigation-link{margin-top:15px;margin-left:20px;transition-duration:.5s}.navigation-link:hover{color:#CBB3BF;transition-duration:.5s}

.home-row-wrapper{height:100%}.home-title-wrapper{background-color:white;display:flex;justify-content:center;align-items:center}.home-description-wrapper{background-color:#8DA1B9;display:flex;justify-content:center;align-items:center}

.about-row-wrapper{height:50%;display:flex;flex-direction:column}.about-title-wrapper{color:white;background-color:#CBB3BF;display:flex;justify-content:center;align-items:center}.about-description-wrapper{color:black;background-color:white;display:flex;justify-content:center;align-items:center;margin-top:25px !important}

.works-row-wrapper{height:100%}.works-title-wrapper{color:white;background-color:#95ADB6}.works-description-wrapper{color:black;background-color:white}

.contact-row-wrapper{height:50%;display:flex;flex-direction:column}.contact-title-wrapper{color:black;background-color:white;display:flex;justify-content:center;align-items:center}.contact-description-wrapper{color:white;background-color:#8B8599;display:flex;justify-content:center;align-items:center;flex-direction:column;margin-top:25px !important}.contact-link-wrapper{margin-top:25px !important}.contact-link{margin-right:10px;margin-left:10px;color:white;transition-duration:.5s}.contact-link:hover{color:#95ADB6;transition-duration:.5s}

